const libraries = (
  state = {
    libraries: [
      {
        value: "-1",
        label: "No library found"
      }
    ],
    selectedLibrary: {
      value: "-1",
      label: "No library found"
    }
  },
  action
) => {
  switch (action.type) {
    case "LOAD_LIBRAIES":
      return (state = {
        ...state,
        libraries: action.payload,
        selectedLibrary: action.payload[0]
      });
    case "SELECT_LIBRARY":
      return (state = {
        ...state,
        selectedLibrary: state.libraries[action.payload]
      });
    default:
      return state;
  }
};
export default libraries;



